.examples-container {
  padding: 40px 0;

  .flex-it-2 {
    @include small-mq {
      flex: 0 1 100% !important;
    }
  }

  .example-tile {
    margin-bottom: 4em;
    background: #FEFEFE;
    border: 1px solid #EBEBEB;
    position: relative;
    text-align: center;
    overflow: hidden;


    a {
      color: #FFF;
      line-height: 5;
      padding: 0 1em;

      @include small-mq {

        figcaption {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          width: 100%;
          display: block;
          height: 50px;
          line-height: 1;
          font-size: 12px;
          top: 30px;
        }
      }
    }


    figure {
      img {
        width: 100%;
      }
    }

    figcaption {
      width: 100%;
      height: 100%;
      text-indent: 0;
      left: 0;
      background: darken($accent-color, 0.95);
      text-transform: uppercase;
      font-size: 1.6em;
      font-weight: bold;
      color: #FFF;
      transform: scale(0);
      z-index: 2;
      line-height: 18;
      transition: transform 0.2s ease;
      position: absolute;
      visibility: visible;
    }

    &:hover {

      figcaption {
        transform: scale(1);
        top: 0;
      }
    }
  }
}

.form-control {
  width: 100%;
  box-sizing: border-box;
  padding-left: 16px;
  line-height: 40px;
  height: 40px;
  border: 1px solid #CCCCCC;
  border-radius: 3px;
  outline: none;
}

.form-group {
  margin-bottom: 1em;
  clear: both;

  label {
    display: block;
    font-weight: bold;
    font-size: .8em;
  }
}

#map-example-container,
#map-example-container-paris {
  // trigger zindex value otherwise
  // map goes hover other elements (like header)
  position: relative;
  z-index: 0;
}

body.example { // sass-lint:disable-line no-qualifying-elements

  .cm-navigation {
    z-index: 99999;
    top: 0;
    border-bottom: 1px solid darken(#1D2F40, 10%);
  }

  aside {
    float: left;
    top: 130px;
  }

  .content-wrapper {
    top: 60px;
  }

  #results-topbar {
    min-height: 60px;
  }

  .btn {
    color: black;
  }

}

#examples {
  ul {
    margin-top: 20px;
  }

  ul > li {
    clear: none;
  }

  h2 {
    clear: both;
  }
}
